  .list-cont{
    width:97%;
    position: relative;

  }
  .show-hidden-board{
    width:90%;
    height: 200px;
    box-shadow: 0px 0px 2px 1px rgb(133, 131, 131);
    border: 0px solid;
    background-color: rgba(255, 255, 255, 0.432);
    border-radius: 5px;
    margin: 0 10px 20px 20px;
  }
  .leaderboard__profile {
    width: 96%;
    border:0px solid;
    display: grid;
    grid-template-columns: 10% 20% 20% 20% 15% 15%;
    align-items: center;
    column-gap: 10px;
    padding: 10px 10px 10px 10px;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 0 5px 7px -1px rgba(51, 51, 51, 0.23);
    /* cursor: pointer; */
    transition: transform 0.25s cubic-bezier(0.7, 0.98, 0.86, 0.98), box-shadow 0.25s cubic-bezier(0.7, 0.98, 0.86, 0.98);
    background-color: #fff;
    margin-bottom: 7px;
  }
  .leaderboard__profile:hover {
    transform: scale(1.0001);
    box-shadow: 0 9px 47px 11px rgba(51, 51, 51, 0.18);
  }
  .leaderboard__picture {
    max-width: 100%;
    width: 60px;
    border-radius: 50%;
    box-shadow: 0 0 0 10px #ebeef3, 0 0 0 22px #f3f4f6;
  }
  .leaderboard__name {
    color: #979cb0;
    font-weight: 500;
    font-size: 1.5vw;
    letter-spacing: 0.64px;
    margin-left: 10px;
  }
  .leaderboard__value {
    color: #1f0dc2;
    font-weight: 700;
    font-size: 34px;
    text-align: right;
    /* text-decoration: underline; */
    
  }
  .leaderboard__value > span {
    float: left;
    opacity: 0.8;
    font-weight: 500;
    font-size: 12px;
    margin-left: 3px;
  }
  

  .leaderboard {
    box-shadow: 0 0 40px -10px rgba(0, 0, 0, 0.4);
  }
