.modal-overlay{
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: #333;
    opacity: 0.7;
    z-index: 1000;
}
.modal-cont{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50% ,-50%);
    z-index: 1000;
    border-radius: 10px;
    box-shadow: 0px 0px 10px 0px #fff;
    padding: 0px;   
}
.modal-relative{
  position: relative;
  width: 100%;
  height: 100%;
}

.modal-top-bar{
  width: 100%;
  /* border: 1px solid; */
  height: 50px;
  position: absolute;
  z-index: 1;

}
#close{
position: absolute;
top: 1%;
right: 1%;
background-color: transparent;
height: 4vw;
width: 4vw;
cursor: pointer;
transition: 0.7s;
z-index: 1000;
border: 0;
}

            
           /* scroll bar  */
/* width */
::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f100; 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888; 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555; 
  }
@media screen and (max-width:700px) {
  .modal-cont{
    width:fit-content !important;
}
#close{
  top: 1%;
  right: 10%;
  z-index: 1100;
  /* display: none; */
  }
 
}