@import 'react-round-carousel/src/index.css';
.carousel__slide{
    border:0px !important;
    border-radius: 3px;
    cursor: pointer;
    /* width:100px !important */
}
.carouselp{
    /* width:100px !important;
    font-size: 10px; */
}
.carouselp img{
    /* width: 100px !important; */
}
.carousel__slide{
    background-color: rgba(128, 128, 128, 0.747) !important;
    border:0px solid !important;
    /* position: relative; */
    height: 200px;
    width:200px !important;
}
.carousel__slide img{
    /* border:1px   ; */
    position: absolute;
    top:50%;
    transform: translate(0%,-50%);
}
.slider-cont{
    margin: 100px 0 50px 0;
}
.slider-title-cont{
    padding: 0px 35px 0 10px

}
.slider-title{
    border-radius:5px ;
    /* padding: 0px !important; */
    background-color: rgba(128, 128, 128, 0.13);
    width: 90%;
    text-align: start;
    line-height: 100px;
    height: 100% !important;
}
@media only screen and (max-width:900px){
    .carousel__slide{
        background-color: rgba(128, 128, 128, 0.747) !important;
        border:0px solid !important;
        /* position: relative; */
        height: 100px;
        width:100px !important;
    }
    .slider-cont{display: none;}
}