.textarea{
    width: 100% !important;
    border-radius: 7px;
    padding: 10px;
}
.new-appointment-form{

    height: 80%;
}
.new-appointment-form,.new-appointment-form .form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* border: 1px solid; */
    width: 100%;
}
.new-appointment-form .form {
margin-top:20px ;
align-items: center !important;

}
.appointment-submit-btn{
    margin-top: 20px;
    background-color: rgba(0, 0, 0, 0.137);
    border: 0px;
    color: #fff;
    padding: 8px;
    width: 80px;
    cursor: pointer;
}
.appointment-submit-btn:hover{
    background-color: rgba(0, 0, 0, 0.815);
    margin-top: 15px;
    border: 5px solid gray;
}
.new-appooint label{
    color: rgba(0, 0, 0, 0.6);
    font-size: 24px;
line-height: 28px;
}
.new-appooint input{
    width: 130px;
    font-size: 22px;
}