.metion-container{
    padding-bottom: 10px;
    margin: 100px 0px 50px 0px;
    min-height: 300px;
    height: fit-content;
    /* padding: 0 10px 0 25px; */
}

.home-board-nav .sub-nav-bttn {
    width: 200px;
    height: fit-content;
    font-size: 20px;
    line-height: 50px;
    background-color: rgb(0, 0, 0) !important; ;
    color: #ffffff;
    text-transform: capitalize;
    border: 1px solid #9CA1AA;
    cursor: pointer;
    border-radius: 3px;
    background-color: #ffb30013;
    transition: 0.5s ease-in-out
}

.sub-nav-bttn:hover {
    background-color: #ffb300b9;
    transition: 0.3s;
    transform: scale(1.1)
}
.home-board-nav div{
    height: 20%;
}
.board-home {
    /* border:1px solid ; */
    padding: 0px 20px 0px 30px;
    margin: 0px;
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    /* flex-grow: inherit; */
    justify-content: center;
    align-items: center;
    align-content: center;
    text-align: start;
    text-justify: distribute;
    line-height: 30px;
    word-spacing: 10px;
    font-size: 18px;
    color: rgba(245, 242, 242, 0.952);
    font-weight: 800;
    background-color: rgba(0, 0, 0, 0.589);
    border: 1px solid;
}

/* board */


.home-board-nav {
    width: 300px;
    flex-direction: column;
    align-items: flex-end;
    height: 400px;
}

.home-board-text {
    height: 400px;
    width: 900px;
    flex-grow: 1;
}

/*  */
.board-home-text-cont {
    padding: 0px;
    border: 1px solid;
    /* display: table; */
    width: 80%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-grow: 0;
    background-color: rgba(0, 0, 0, 0.253);
    background-image: url(../../icons/louis-moinet/Louis-Moinet-1.jpg);
    background-attachment: fixed;
    background-size: cover;

}
@media only screen and (max-width:900px){
    .metion-container{
        padding-bottom: 10px;
        margin: 100px 0px 50px 0px;
        min-height: 300px;
        height: fit-content;
        padding: 0 0px 0 0px;
    }
    .home-board-cont {
        flex-direction: column;

    }
    .home-board-nav{
        width: 100%;
        /* height: fit-content !important; */
        flex-direction: row ;
        height: 100px ;
       /* display: grid !important; */
       /* grid-template-columns: 1rem 1rem 1rem 1rem 1rem !important; */
       /* grid-template-rows: 30px repeat(2); */
       /* margin-bottom: 15px; */
        /* background-color: #9CA1AA !important; */
        padding: 0px !important;
        flex-wrap: wrap ;
    }
    .home-board-nav div{
        /* display: flex; */
        width: fit-content;
        height: 50px;
        border:1px solid;
        flex-grow: 1;
        flex: 0 30%;
    }
    .sub-nav-bttn {

        width:100% !important;
        height: 50px !important; 
        font-size: 12px !important;
        
    }
    .board-home-text-cont {
        width: 90%;
        
    }
    .home-board-text{
        width:100%;
        height: fit-content;
    }
    .board-home{
        font-size: 0.71rem;
        line-height: 40px;

    }
}