.subhero-cont{
    margin:100px 0 50px 0
}
.subh-content ul{
   list-style: square ;
   list-style-type: square;
   list-style-image: -moz-linear-gradient(red, blue);
   /* padding: 15px; */
   border:1px solid;
   width: 100%;
   height: 100%;
   border-style: groove;
}
.subh-content ul h1{
    letter-spacing: 3px;
}
.subh-content ul li{
    font-size:25px;
    padding: 5px;
    letter-spacing: 2px;
    
}
.sub-hero-img{
    width:100%;
    height: 100%;
}
.subh-img{
    border-radius: 10px 20px 0px 20px;
    background-color: white;
    border:1px solid black;
}
@media only screen and (max-width:900px){
    .sub-sub-hero-cont{ flex-direction: column;}
    .subh-content{
        width: 88% !important;
        padding: 0px !important;
        margin: 0px;
    }
    .subh-img{
        width:100% !important;
    }
}