.hero-Cont{
    background-color: black;
    width: 100vw;
}
.hero-section{
  position: relative;
  /* border:1px solid; */
  max-height:100vh !important;
  height: 100vh;
  width: 100%;
  
}
.ovc{
  margin: 0px;
}
.hero-sec-text{
width:42%
}

.hero-sec-adjacent{
  width:41%
}
#background-video{
    object-fit: cover;
    top:0px;
    left:0px;
    right: 0px;
    bottom: 0px;
    height: 100% !important;
    width: 100% !important;
    position: absolute;
}
.over-lay{
  background-color: rgba(0, 0, 0, 0.521);
  position: absolute;
  top:0px;
  left:0px;
  bottom: 0px;
  right: 0px;
  z-index: 1;
  color: #fff;
}
.over-lay h1{
  /* letter-spacing: 5px; */
  font-size: 25pt;
}
.over-lay p{
  color: #fff;
  font-size: 10pt;
  font-weight: 60;
  background-color: #00000088;
  /* padding-left: 10px; */
  border-style: groove;
  border-radius: 15px;
  padding: 20px;
  line-height: 30px;
  /* letter-spacing: 2px; */
  border: 0px ;
  margin-top: 100px;
}
.ctn{
  background-color:  rgb(255, 255, 255);
  border-radius: 7px;
  line-height: 30px;
  font-size: 14pt;
  color: rgb(0, 0, 0);
  width: 250px;
  cursor: pointer;
  /* border:1px solid; */
  float: inline-end;
  height: 40px;
}

.ctn-cont{
  /* display: block; */
  display: flex;
  justify-content: flex-end;
  width: 100%; 
  /* border: 1px solid; */
}
.ctn span{
  vertical-align: middle;
  color: rgba(100, 148, 237, 0.589);
}
.ctn:hover{
  background-color:rgba(100, 148, 237, 0.589);
}
  video.fullscreen::-webkit-media-controls {
    display: none !important;
  }
/* .hero-vid-src{
    margin: 0px;
    width: 10%;
    padding: 0px;
} */
@media only screen and (max-width:900px){
  .hero-sec-text{
    padding: 0px 10px 0px 10px;
  }
  .hero-sec-adjacent{
    display: none;
  }
  .over-lay h1{
    font-size: 30px;  
  }
  .hero-sec-text{
    width:80%
    }

    .hero-sec-text p{
      font-size: 13pt;
      text-align: justify;

    } 
    .hero-sec-adjacent{
      width:0%
    }
}