.edit-field{
    /* width: 100%; */
    height: fit-content !important;
    display: grid;
    grid-template-areas: ". . ."" name newvalue icon" "sub . ."    ;
    grid-template-rows:10px 1fr 1fr  ;
    grid-template-columns:1.5fr 1fr 0.5fr;
    padding-left: 20px;
    gap:5px;
    font-size:17px;
    margin:20px 0px 20px;
    padding: 10px 10px 10px;

}
.name{
    grid-area: name;
    font-size: 1.2em;
}
.name-input{
    grid-area: newvalue;
    width: 10em;
    height: 20px;
    background-color: #455680;
    
}
.edit-sub{
    grid-area: sub;
    cursor: pointer;
}
.sub-btn{
    width:70px;
    height: 30px;
}
.edit-icon{
    grid-area: icon;
    cursor: pointer;
    color:#455680
}
@media screen and (max-width:700px) {
    .edit-field{
       
        grid-template-areas: ". . . ."". name . icon" ". newvalue . ."  ". sub . . "  ;
        grid-template-rows:10px 1fr 0.5fr 0.5fr ;
        grid-template-columns:0px 1.5fr 0fr 0.5fr;
        /* padding-left: 20px; */
        gap:5px;
        font-size:17px
    
    }
    
    /* .edit-icon{
