.loginbar-cont {
    position: absolute;
    z-index: 10;
    /* top: -8px; */
    background-color: transparent;
}



.login-btn-cont {
    height: 80px;
    background-color: transparent;
}

.login-form {

    width: 437px;
    height: 437px;
    display: block;
    position: relative;
    background-color: #FFFFFF;
}

.login-cont {
    position: absolute;
    top: 56px;
    left: 40px;
    right: 40px;
    bottom: 50px;
    width: calc(100%-80px);
    display: block;
    border: 0px solid;
}

.login-cont h1 {
    font-family: 'Helvetica';
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 24px;
    /* identical to box height */
    text-align: center;
    margin: 0px;
    color: #000000;
}

.login-cont label {
    font-family: 'Helvetica';
    font-style: normal;
    font-weight: 400;
    font-size: 19px;
    line-height: 19px;
    /* identical to box height */
    color: rgba(0, 0, 0, 0.6);
}

.email,
.pass {
    border: 1px solid rgba(26, 31, 22, 0.25);
    border-radius: 8px;
    padding-left: 20px;
    width:calc(100% - 24px);
    height: 40px;
    font-size: 17px;
}

.submit-login {
    width: 100%;
    height: 52px;
    background-color: #000000;
    border-radius: 8px;
    color: #FFFFFF;
    font-family: 'Helvetica';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
}
.login-cont input , .login-cont button{
    
    margin-bottom: 10px;
}
.err-message{
    color: rgba(255, 32, 32, 0.6);
    font-size: 18px;
    display: inline-block;
    width: 100%;
    text-align: right;
    /* border: 1px solid; */
}
.register-btn{
    font-family: 'Helvetica';
font-style: normal;
font-weight: 400;
font-size: 20px;
color: #000000;
position: absolute;
bottom: 30px;
left: 50%;
transform: translate(-50%);
background-color: transparent;
border: 0px;
/* border-bottom: 1px solid; */
}

.signup-form {
    /* border: 3px solid red; */
    height: 100%;
    background-color: #FFFFFF;
    /* width: 200px; */
    display: grid;
    gap: 10px;
    grid-template-areas:". . ." " . title ." ". name . " ". email ." ". pass ."" . pass1 ." ". err ." ". sub ." ". . .";
    grid-template-rows:20px 80px 80px 80px 80px 80px 30px 80px 10px;
    grid-template-columns: 40px 450px 40px;
    /* padding: 20px; */
}
.signup-form label{
    color: rgba(0, 0, 0, 0.6);
    font-size: 19px;
}
.signup-form input {
    border-radius: 5px;
    padding-left: 20px;
    width: calc(100% - 24px);
    height: 40px;
    font-size: 17px;

}
.title{
    grid-area: title;
    text-align: center;
    font-weight: 700;
    font-size: 34px;
    line-height: 34px;
}
.sign-name {
    grid-area: name;
    border: 0px solid;
}

.sign-email {
    grid-area: email;
}

.sign-pass {
    grid-area: pass;
}

.sign-pass1 {
    grid-area: pass1;
}
.signup-form .err-message{
    display: block;
    grid-area: err;

    height: 33px;
}
.sign-sub {
    grid-area: sub;
    padding-left: 0px !important;
    background: #000000;
    border: 0px;
    color: white;
    height:35px;
    width: 100%;
    margin: 0px !important;
    font-size: 17px;
    border-radius: 5px;
}

.sign-sub:hover {
    background-color: black;
    color: green;
}

/* Modal */
.contact-form {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    min-height: fit-content;
    width: 100%;
    max-width: 100%;
    margin-bottom: 50px;
    padding-bottom: 40px;
    background-color: aliceblue;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@media screen and (max-width:700px) {
    .login-form {

        width: 337px !important;
        height: 437px;
        /* display: block; */
    }
    .signup-form {
        /* border: 3px solid red; */
        height: 100%;
        background-color: #FFFFFF;
        /* width: 200px; */
        display: grid;
        gap: 0px;
        /* grid-template-rows: 80px 80px 80px 80px 80px 80px 10px; */
        grid-template-columns: 40px 260px 40px;
        /* padding: 20px; */
    }
  

}

